import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { SelectedAriaAttributes, FlyoutAriaAttribute, FlyoutFooterBehavior, FlyoutMotionHiddenEndEventDetail, FlyoutMotionVisibleEndEventDetail, FlyoutPosition, Theme } from '../types';

@Component({
  selector: 'p-flyout,[p-flyout]',
  template: '<ng-content />',
  inputs: ['aria', 'disableBackdropClick', 'footerBehavior', 'open', 'position', 'theme'],
  outputs: ['dismiss', 'motionHiddenEnd', 'motionVisibleEnd'],
  standalone: false
})
export class PFlyout extends BaseComponentWithTheme {
  aria?: SelectedAriaAttributes<FlyoutAriaAttribute>;
  disableBackdropClick?: boolean;
  footerBehavior?: FlyoutFooterBehavior;
  open: boolean;
  position?: FlyoutPosition;
  declare theme?: Theme;
  dismiss = new EventEmitter<CustomEvent<void>>();
  motionHiddenEnd = new EventEmitter<CustomEvent<FlyoutMotionHiddenEndEventDetail>>();
  motionVisibleEnd = new EventEmitter<CustomEvent<FlyoutMotionVisibleEndEventDetail>>();
}