import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { SelectedAriaAttributes, SheetAriaAttribute, SheetMotionHiddenEndEventDetail, SheetMotionVisibleEndEventDetail, Theme } from '../types';

@Component({
  selector: 'p-sheet,[p-sheet]',
  template: '<ng-content />',
  inputs: ['aria', 'disableBackdropClick', 'dismissButton', 'open', 'theme'],
  outputs: ['dismiss', 'motionHiddenEnd', 'motionVisibleEnd'],
  standalone: false
})
export class PSheet extends BaseComponentWithTheme {
  aria?: SelectedAriaAttributes<SheetAriaAttribute>;
  disableBackdropClick?: boolean;
  dismissButton?: boolean;
  open: boolean;
  declare theme?: Theme;
  dismiss = new EventEmitter<CustomEvent<void>>();
  motionHiddenEnd = new EventEmitter<CustomEvent<SheetMotionHiddenEndEventDetail>>();
  motionVisibleEnd = new EventEmitter<CustomEvent<SheetMotionVisibleEndEventDetail>>();
}