import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { CanvasSidebarStartUpdateEventDetail, Theme } from '../types';

@Component({
  selector: 'p-canvas,[p-canvas]',
  template: '<ng-content />',
  inputs: ['sidebarEndOpen', 'sidebarStartOpen', 'theme'],
  outputs: ['sidebarEndDismiss', 'sidebarStartUpdate'],
  standalone: false
})
export class PCanvas extends BaseComponentWithTheme {
  sidebarEndOpen?: boolean;
  sidebarStartOpen?: boolean;
  declare theme?: Theme;
  sidebarEndDismiss = new EventEmitter<CustomEvent<void>>();
  sidebarStartUpdate = new EventEmitter<CustomEvent<CanvasSidebarStartUpdateEventDetail>>();
}