import { Component, EventEmitter } from '@angular/core';
import { BaseComponentWithTheme } from '../../utils';
import type { BreakpointCustomizable, CheckboxBlurEventDetail, CheckboxUpdateEventDetail, CheckboxState, Theme } from '../types';

@Component({
  selector: 'p-checkbox,[p-checkbox]',
  template: '<ng-content />',
  inputs: ['checked', 'compact', 'disabled', 'form', 'hideLabel', 'indeterminate', 'label', 'loading', 'message', 'name', 'required', 'state', 'theme', 'value'],
  outputs: ['blur', 'update'],
  standalone: false
})
export class PCheckbox extends BaseComponentWithTheme {
  checked?: boolean;
  compact?: boolean;
  disabled?: boolean;
  form?: string;
  hideLabel?: BreakpointCustomizable<boolean>;
  indeterminate?: boolean;
  label?: string;
  loading?: boolean;
  message?: string;
  name?: string;
  required?: boolean;
  state?: CheckboxState;
  declare theme?: Theme;
  value?: string;
  blur = new EventEmitter<CustomEvent<CheckboxBlurEventDetail>>();
  update = new EventEmitter<CustomEvent<CheckboxUpdateEventDetail>>();
}